<template>
	<div id="admin">
		<header>
				<div class="btn-logout" @click="logout">
					<span class="logout-icon"></span>
					<span>退出</span>
				</div>
		</header>
		<section class="container">
			 <aside class="aside">
				<button id="btn-toggle" :class="isCollapse?'down':'up'" @click="toggleCollapse"></button>
				<div class="leftMenu">
					<el-menu default-active="1-4-1" class="el-menu-vertical-demo" 
					@open="handleOpen" @close="handleClose" :collapse="isCollapse"
					 background-color="#545c64"
					text-color="#fff"
					active-text-color="#ffd04b">
					<el-submenu index="1">
						<template slot="title">
							<i class="el-icon-location"></i>
							<span slot="title">导航一</span>
						</template>
						<el-menu-item-group>
							<span slot="title">分组一</span>
							<el-menu-item index="1-1">选项1</el-menu-item>
							<el-menu-item index="1-2">选项2</el-menu-item>
						</el-menu-item-group>
						<el-menu-item-group title="分组2">
							<el-menu-item index="1-3">选项3</el-menu-item>
						</el-menu-item-group>
						<el-submenu index="1-4">
							<span slot="title">选项4</span>
							<el-menu-item index="1-4-1">选项1</el-menu-item>
						</el-submenu>
					</el-submenu>
					<el-menu-item index="2">
						<i class="el-icon-menu"></i>
						<span slot="title">导航二</span>
					</el-menu-item>
					<el-menu-item index="3" disabled>
						<i class="el-icon-document"></i>
						<span slot="title">导航三</span>
					</el-menu-item>
					<el-menu-item index="4">
						<i class="el-icon-setting"></i>
						<span slot="title">导航四</span>
					</el-menu-item>
				</el-menu>
				</div>
			
			 </aside>
			 <section class="main" style="margin-left:200px;"><router-view></router-view></section>
		</section>

		
	</div>
</template>

<script>
import {Ajax} from '@/js/ajax.js'
export default {
	data() {
      return {
        isCollapse: false
      };
    },

	created () {

	},
	mounted () {

	},
	methods: {
		toggleCollapse(){
			this.isCollapse=!this.isCollapse
			this.setMainLeft(this.isCollapse);
		},
		handleOpen(key, keyPath) {
			console.log(key, keyPath);
		
		},
		handleClose(key, keyPath) {
			console.log(key, keyPath);
		},
		setMainLeft(isCollapse){
				let mainObj = document.getElementsByClassName('main')[0];
				if(isCollapse){
					mainObj.style.marginLeft='50px'
				}else{
						mainObj.style.marginLeft='200px'
				}
				console.log(mainObj.style.marginLeft);
		},
		 //退出登录
    logout:function(){
      let that=this;
      this.$confirm('用户将退出登录，请确认是否继续？', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'info',
        center:true,
      }).then(() => {
					Ajax({
					method:'post',
					url : httpConfig+'logout',
					data : {},
					done : function(error,data,resdata) {
						if(error){
							that.$message({
								showClose: true,
								message: error.msg,
								type: 'error'
							});
						}else{
							that.$router.push('/login');
						}	
					},function(error) {
							that.$message({
								showClose: true,
								message: error.msg,
								type: 'error'
							});
					},					
				})
      
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '取消退出'
        });          
      });
    },
	}
}
</script>
<style lang="scss" src="@/styles/admin.scss" scoped>
	
</style>








